<template>
 <div>
   <ul>
     <li class="list clearfix" v-for="(item,index) in dataList" :key="index">
       <div @click="detail(item.id)" class="left">
        <img :src="item.cover.url" alt="">
       </div>
       <div class="right">
         <h4>{{item.title}}</h4>
         <span v-for="(item,index) in item.actors" :key="index">{{item}}/</span>
         <p>{{item.year}}年</p>
       </div>
     </li>
   </ul>
   <div class="loading" v-show="!isLoading">
     <div class="loading-content">
       <img src="../../assets/img/loading.gif" alt="">
     </div>
   </div>
 </div>
</template>
<script>
import axios from "axios"
export default {
  created(){
    this.getData();
  },
  methods: {
    detail(id){
      console.log(id);
      this.$router.push({name:"moviedetail",params:{movieId:id}})
    },
    getData(){
      let proxy = 'https://bird.ioliu.cn/v2?url='
      let url = `https://m.douban.com/rexxar/api/v2/subject_collection/movie_showing/items?start=${this.listStart}&count=10`
      this.isLoading = false;
      axios.get(proxy+url)
      .then((res)=>{
        console.log(res);
        this.dataList = this.dataList.concat(res.data.subject_collection_items);
        this.isLoading = true;
      }) 
    .catch((res)=>{
        // console.log('失败');
      })
    }
  },
  data(){
    return{
      dataList:[],
      listStart:0,
      isLoading:true,
    }
  },mounted() {
     window.onscroll = () =>{
      let clientHeight = document.documentElement.clientHeight;//可视区域高度
      let scrollTop = document.documentElement.scrollTop;//当前滚动高度
      let scrollHeigth = document.documentElement.scrollHeight;//滚动条可滚动高度
      // console.log(clientHeight,scrollTop,scrollHeigth)
      if(clientHeight+scrollTop>=scrollHeigth-10 && this.isLoading && this.dataList.length !=25){
        this.listStart+=10;
        this.getData()
      }
    }
  },
}
</script>
<style scoped>
  ul{
    padding: 10px;
  }
  .list{
    border-bottom: 1px solid #333;
    padding: 20px 0;
  }
  .list .left{
    float: left;
    width: 40%;
    text-align: center
  }
  .list .left img{
    width: 80%;
  }
  .list .right{
    float: left;
    width: 55%;
    padding-left:5%
  }
  .loading{
    background-color: rgba(0,0,0,.3);
    position: fixed;
    top:0;
    left:0;
    right: 0;
    bottom: 0;
  }
  .loading-content{
    position: absolute;
    left: 50%;
    top:50%;
    margin-left: -75px;
    margin-top:-75px;
    width: 150px;
    height: 150px;
    background-color: rgba(255,255,255,.6);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
</style>
